<!--图片功能组件-->
<template>
  <div class="">
    <!--按钮-->
    <div class="btn">
      <el-radio-group v-model="status" size="small" @change="changeStatus">
        <el-radio-button :label="false">全部</el-radio-button>
        <el-radio-button :label="true">收藏</el-radio-button>
      </el-radio-group>
      <!--上传按钮-->
      <el-button size="small" type="success" @click="dialogTableVisible=true" v-if="uploadBtn">上传图片</el-button>
    </div>

    <!--图片-->
    <el-row :gutter="10">
      <el-col :xs="12" :sm="6" :md="6" :lg="4" v-for="(img,index) in images" :key="index"  class="image-item">
        <el-image
            :lazy="true"
            fit="cover"
            placeholder="图片正在加载"
            style="height: 120px"
            :src="img.imgUrl"
            :preview-src-list="[img.imgUrl]">
          <div slot="error" class="image-slot">
            <i class="el-icon-picture-outline"></i>
          </div>
        </el-image>
        <div class="image-action" >
          <el-button type="warning" :icon="img.imgFlag?'el-icon-star-on':'el-icon-star-off'" circle size="small" @click="onCollect(img)" v-if="favoritesDeletionBtn"></el-button>
          <!--class 样式绑定 {css类名：布尔值} true：使用 false： 不使用-->
          <!--            <i :class="{'el-icon-star-on':img.flag,'el-icon-star-off':!img.flag}" @click="onCollect(img.flag)"></i>-->
          <!--            <i class="el-icon-delete-solid"></i>-->
          <el-button type="danger" icon="el-icon-delete" circle size="small" @click="deleteImag(img)" v-if="favoritesDeletionBtn"></el-button>
          <el-button type="success" icon="el-icon-check" circle size="small" v-if="!favoritesDeletionBtn" @click="selectBtn(img)"></el-button>
        </div>
      </el-col>
    </el-row>

    <!--弹出层-->
    <el-dialog title="上传图片" :visible.sync="dialogTableVisible" :append-to-body="true" width="400px">
      <el-upload
          v-if="dialogTableVisible"
          class="upload-demo"
          drag
          action="http://localhost:9090/image/ossUpload"
          :on-success="imgUpload"
          multiple>
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
      </el-upload>
    </el-dialog>

    <!--分页-->
    <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        background
        :current-page.sync="currentPage"
        :page-sizes="[12, 16, 20, 24]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
    </el-pagination>
  </div>
</template>

<script>
import request from "../../utils/request";

export default {
  name: "ImgFunctionIndex",
  components: {},
  props:{
    //上传按钮是否展示
    uploadBtn: {
      type: Boolean,
      default: true //默认展示
    },
    //图片收藏删除选中按钮是否展示
    favoritesDeletionBtn: {
      type: Boolean,
      default: true //默认展示
    }
  },
  data() {
    return {
      currentPage: 1,//当前页码
      pageSize:12,//当前每页显示条数
      total: 0,//总记录数
      dialogTableVisible:false,//弹出层状态
      status:false,//按按钮状态全部或者收藏
      flag:'',//向后台传入状态
      images:[
        {
          id:'',//图片id
          imgFlag:'',//图片是否收藏
          imgUrl:'',//图片url
        }
      ],//图片集合
      imgData:{},
    }
  },
//页面加载
  created() {
    //获取图片列表
    this.loadImagePage()
  },
//可以访问DOM元素
  mounted() {
  },
//方法集合
  methods: {
    //改变当前每一页个数
    handleSizeChange(pageSize) {
      this.pageSize=pageSize
      //获取图片列表
      this.loadImagePage()
    },
    //改变当前页码触发
    handleCurrentChange(pageNum) {
      this.currentPage=pageNum
      //获取图片列表
      this.loadImagePage()
    },
    //获取图片列表
    loadImagePage(){
      request.get("/image/getTypePage",{params:{
          pageNum:this.currentPage,//向后台传入的页码
          pageSize:this.pageSize,//向后台传入的当前页显示条数
          search:this.flag,//向后台传入的搜索条件
        }}).then(res=>{
        //设置总记录数
        this.total=res.data.data.imagePage.total
        //设置表格数据
        this.images=res.data.data.imagePage.results
      })
    },
    //该变按钮状态触发方法
    changeStatus(value){
      if(value){
        this.flag=true
      }else {
        this.flag=''
      }
      this.loadImagePage()
    },
    //点击改变是否收藏触发(传入是否收藏状态)
    onCollect(img){
      request.patch("/image/updateImgFlagById",img).then(res=>{
        if(res.data.result){
          this.$message.success(res.data.message)
        }else {
          this.$message.error(res.data.message)
        }
        //重新加载数据
        this.loadImagePage();
      })
    },
    //点击删除按钮
    deleteImag(img){
      request.delete("/image/deleteImgById/"+img.id).then(res=>{
        if(res.data.result){
          this.$message.success(res.data.message)
        }else {
          this.$message.error(res.data.message)
        }
        //重新加载数据
        this.loadImagePage();
      })
    },
    //图片上传成功触发的方法
    imgUpload(response,file,fileList){
      //关闭弹框
      this.dialogTableVisible=false
      //刷新数据
      this.loadImagePage()
    },
    //选中按钮方法
    selectBtn(img){
      this.imgData=img
      this.$emit("imgData",this.imgData)
    },
  },
}
</script>

<style scoped>
.btn{
  margin: 10px;
  display: flex;
  justify-content: space-between;
}
.image-item{
  position: relative;
}
.image-action{
  font-size: 20px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: #fff;
  height: 40px;
  background-color: rgba(0,0,0,0.5);
  position: absolute;
  bottom: 4px;
  left: 5px;
  right: 5px;
  transition: 0.5s;
  opacity: 0;
}
.image-item:hover .image-action{
  opacity: 1;
}
/*设置图片宽度*/
.el-image{
  width: 100%;
}
</style>
        
