<!--博客新增页-->
<template>
  <div >
    <el-form ref="ruleForm" :model="ruleForm" :rules="rules"  style="width: 80%;" label-width="100px">
      <!-- 博客标题     -->
      <el-form-item label="标题" prop="title" >
        <el-input placeholder="请输入博客标题" v-model="ruleForm.title" class="input-with-select">
          <el-select v-model="flag" slot="prepend" placeholder="请选择" style="width: 100px">
            <el-option label="原创" value="true"></el-option>
            <el-option label="转载" value="false"></el-option>
          </el-select>
        </el-input>
      </el-form-item>

      <el-row>
        <el-col :span="12"><div class="grid-content bg-purple">
          <el-form-item label="博客标签" prop="firstPicture">
            <!--el-select属性：clearable添加删除功能 filterable表示可以搜索            -->
            <el-select   v-model="tagNames"  clearable multiple filterable allow-create default-first-option placeholder="请选择博客标签">
              <el-option v-for="item in tags" :value="item.id"   :label="item.name"></el-option>
            </el-select>
          </el-form-item>
        </div></el-col>
        <el-col :span="12"><div class="grid-content bg-purple-light">
          <el-form-item label="博客分类" prop="firstPicture">
            <el-select   v-model="types" clearable  filterable allow-create default-first-option placeholder="请选择博客分类">
              <el-option v-for="item in type" :value="item.id"   :label="item.name"></el-option>
            </el-select>
          </el-form-item>
        </div></el-col>
      </el-row>
      <!-- 博客内容     -->
      <el-form-item  prop="content" >
        <mavonEditor v-model="ruleForm.content"/>
      </el-form-item>
      <!-- 博客摘要    -->
      <el-form-item label="摘要" prop="description"  >
        <el-input v-model="ruleForm.description" maxlength="100" placeholder="请输入博客摘要" show-word-limit type="textarea"/>
      </el-form-item>
      <!-- 博客声明    -->
      <el-form-item label="声明" prop="statementMessage"  >
        <el-input v-model="ruleForm.statementMessage" maxlength="100" placeholder="请输入博客摘要" show-word-limit type="textarea"/>
      </el-form-item>
      <!--博客首图      -->
      <el-form-item label="首图" prop="imageUrl">
        <div style="height: 120px;width: 200px;border: 1px solid">
        <!-- 图片部分         -->
          <el-image
              style="width: 200px; height: 120px"
              :src="ruleForm.imageUrl"
              :preview-src-list="[ruleForm.imageUrl]">
          </el-image>
        </div>
        <el-button type="primary" @click="open">更换图片</el-button>
      </el-form-item>
      <!-- 功能选项按钮     -->
      <el-form-item >
        <el-row>
          <el-col :span="6"><div class="grid-content bg-purple">
            <el-checkbox v-model="ruleForm.appreciation" label="赞赏"></el-checkbox>
          </div></el-col>
          <el-col :span="6"><div class="grid-content bg-purple-light">
            <el-checkbox v-model="ruleForm.commentabled" label="评论"></el-checkbox>
          </div></el-col>
          <el-col :span="6"><div class="grid-content bg-purple">
            <el-checkbox v-model="ruleForm.shareStatement" label="博客声明"></el-checkbox>
          </div></el-col>
          <el-col :span="6"><div class="grid-content bg-purple-light">
            <el-checkbox v-model="ruleForm.recommend" label="推荐"></el-checkbox>
          </div></el-col>
        </el-row>
      </el-form-item>
      <!-- 表单按钮     -->
      <el-form-item>
        <el-button @click="goOut">返回</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
        <el-button type="primary" @click="insert('ruleForm')">发布</el-button>
      </el-form-item>

    </el-form>

    <!--弹出层   -->
    <el-dialog
        title="选择图片"
        :visible.sync="dialogVisible"
        append-to-body
        width="50%"
        :before-close="handleClose">
      <!--选项卡部分-->
      <el-tabs v-model="activeName" >
        <!--图片库-->
        <el-tab-pane label="图片库" name="first">
          <!--uploadBtn上传按钮是否展示  favoritesDeletionBtn图片收藏删除按钮是否展示-->
          <ImgFunctionIndex :uploadBtn="false" :favoritesDeletionBtn="false" @imgData="getImgData"/>
        </el-tab-pane>
        <!--上传图片-->
        <el-tab-pane label="上传图片" name="second">
          <el-upload
              class="upload-demo"
              drag
              action="http://localhost:9090/image/ossUpload"
              :on-success="imgUpload"
              multiple>
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>
<script>
import { mavonEditor } from 'mavon-editor'
import "mavon-editor/dist/css/index.css";
import router from "../../router";
import {selectAllType} from "../../api/type";
import {selectAllTag} from "../../api/tag";
import request from "../../utils/request";
//导入图片组件
import ImgFunctionIndex from "../imgFunction"
export default  {
  name: "releaseBlogIndex",
  components: {
    mavonEditor,
    ImgFunctionIndex
  },
  data() {
    return {
      dialogVisible: false,//弹出层状态
      activeName: 'second',//弹出层标签页默认值
      // url:'',
      ruleForm: {
        appreciation:true,//是否开启赞赏
        commentNumber:'',//评论数
        commentabled:true,//是否开启评论
        content:'',//文章内容
        createTime:'',//文章创建时间
        description:'',//文章摘要
        flag: '',//原创或者转载
        id:'',//文章id
        imageId:'',//文章首图id
        imageUrl:"",//首图URL
        published:true,//文章是否发布
        recommend:false,//是否为推荐文章
        shareStatement:true,//是否开启声明
        statementMessage:'',//文章声明
        title: '',//博客标题
        typeId: '',//文章分类id
        typeName:'',//文章分类名称
        updateTime:'',//文章更新时间
        views: '',//文章浏览量

        tagList:'',//博客标签id集合
        tagName:'',//博客标签名字集合

      },
      type:'',//博客分类
      types:'',

      tags:[],//博客标签
      tagIds:'',//根据文章id获取的文章标签id集合
      tagNames:'',//根据文章id获取的文章标签名称集合
      flag:'',//博客是否原创
      uploadPath:'',//文件上传访问的后台接口
      rules: {
        title: [{required: true, message: '标题不能为空', trigger: 'blur',},],
        imageUrl:[{required: true, message: '首图不能为空', trigger: 'blur',},],
        description:[{required: true, message: '摘要不能为空', trigger: 'blur',},],
      },
    }
  },
  created() {
    this.loadType()//获取所有分类
    this.loadTag()//获取所有标签
    this.logBlog()//获取文章信息
  },
  methods: {
    //保存按钮
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.ruleForm.published=false//设置为发布形式
          //处理标签分类状态
          this.setUp()
          const _this=this
          //延迟1秒解决请求顺序问题
          setTimeout(function (){
            //判断是否存在id
            if(_this.ruleForm.id!==""){
              //执行更新方法
              _this.updateBlog()
            }else {
              //添加内容
              _this.add();
            }
            //清除浏览器文章记录
            sessionStorage.removeItem("blog")
            //返回到博客页面
            router.push({path:'/layout/blogs'})
          },1000)

        } else {
          return false
        }
      })
    },
    //发布按钮
    insert(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.ruleForm.published=true//设置为草稿形式
          //处理标签分类状态
          this.setUp()
          const _this=this
          //延迟1秒解决请求顺序问题
          setTimeout(function (){
            //判断是否存在id
            if(_this.ruleForm.id!==""){
              //执行更新方法
              _this.updateBlog()
            }else {
              //添加内容
              _this.add();
            }
            //清除浏览器文章记录
            sessionStorage.removeItem("blog")
            //返回到博客页面
            router.push({path:'/layout/blogs'})
          },1000)
        } else {
          return false
        }
      })
    },
    //处理标签分类状态
    setUp(){
      //设置文章分类
      this.ruleForm.typeId=this.types
      //设置文章标签
      //判断内容是否是数字
      if(!isNaN(this.tagNames.join(",")[0])){
        this.ruleForm.tagList=this.tagNames.join(",")
      }else {
        request.get("/tag/getTagByNames/"+this.tagNames.join(",")).then(res=>{
          //获取对应id传入表单
          this.ruleForm.tagList=res.data.data.ids
        })
      }
      //设置文章状态
      if(this.flag==="原创"){
        this.ruleForm.flag=true
      }else if(this.flag==="true"){
        this.ruleForm.flag=true
      }else {
        this.ruleForm.flag=false
      }
    },
    //修改方法
    updateBlog(){
      //修改文章表数据
      request.patch("/blog/editBlog",this.ruleForm).then(res=>{
        if(res.data.result){
          this.$message.success("更该成功")
        }else {
          this.$message.error("更该失败")
        }
      })
    },
    //获取选中图片信息
    getImgData(val){
      this.ruleForm.imageId=val.id
      this.ruleForm.imageUrl=val.imgUrl
      //关闭弹框
      this.dialogVisible=false
    },
    //点击更换图片打开弹出层
    open(){
      //打开弹出层
      this.dialogVisible=true
    },
    //图片上传成功触发的方法
    imgUpload(response,file,fileList){
      if(response.result){
        //上传成功
        this.ruleForm.imageId=response.data.image.id
        this.ruleForm.imageUrl=response.data.image.imgUrl
        this.$message.success("图片上传成功")
        //关闭弹框
        this.dialogVisible=false
      }else {
        //上传失败
        this.$message.error("图片上传失败请重新上传")
      }
    },
    //弹窗关闭
    handleClose(done) {
      this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
    },
    //获取所有分类
    loadType(){
      selectAllType().then(res=>{
        this.type=res.data.data.types
      })
    },
    //获取所有标签
    loadTag(){
      selectAllTag().then(res=>{
        this.tags=res.data.data.tagList
      })
    },
    //添加方法
    add(){
      request.post("/blog/addBlog",this.ruleForm).then(res=>{
        if(res.data.result){
          this.$message.success("操作成功")
        }else {
          this.$message.error("操作失败")
        }
      })
    },
    //返回按钮触发方法
    goOut(){
      sessionStorage.removeItem("blog")
      router.push({path:'/layout/blogs'})
    },
    //获取文章信息
    logBlog(){
      if(sessionStorage.getItem("blog")!=null){
        //获取浏览器文章信息
        this.ruleForm=JSON.parse(sessionStorage.getItem("blog"))
        //获取文章标签id集合信息
        this.tagIds=JSON.parse(sessionStorage.getItem("tagIds")).tagIds.split(",")
        //获取文章标签名称集合信息
        this.tagNames=JSON.parse(sessionStorage.getItem("tagIds")).tagNames.split(",")
        //数组文章状态
        this.flag=this.ruleForm.flag==true? this.ruleForm.flag="原创":this.ruleForm.flag="转载"
        //设置分类
        this.types=this.ruleForm.typeId
      }
    },
  },
}
</script>

<style scoped>
.mavonEditor {
  width: 100%;
  height: 100%;
}

</style>